<template>
  <div>
    <!-- 抽出期間 -->
    <b-row>
      <b-col cols="12" xl="1"></b-col>
      <b-col cols="12" xl="2">
        <label class="title-fw title-fs">{{ $t("EXTRACTIONPERIOD") }}</label>
      </b-col>
      <b-col cols="12" xl="3">
        <b-input-group>
          <b-form-input
            v-model="dateFrom"
            type="text"
            autocomplete="off"
          ></b-form-input>
          <b-input-group-append>
            <b-form-datepicker
              @context="onContext"
              v-model="dateFrom"
              button-only
              right
            ></b-form-datepicker>
          </b-input-group-append>
        </b-input-group>
      </b-col>
      <b-col cols="12" xl="auto">～</b-col>
      <b-col cols="12" xl="3">
        <b-input-group>
          <b-form-input
            v-model="dateTo"
            type="text"
            autocomplete="off"
          ></b-form-input>
          <b-input-group-append>
            <b-form-datepicker
              @context="onContext"
              v-model="dateTo"
              button-only
              right
            ></b-form-datepicker>
          </b-input-group-append>
        </b-input-group>
      </b-col>
    </b-row>
    <b-row>
      <b-col cols="12" xl="9"></b-col>
      <b-col cols="12" xl="2">
        <b-button variant="primary"> {{ $t("SHOW") }}</b-button>
      </b-col>
    </b-row>
    <hr class="hrBarType" />
    <!-- 費目別契約獲得数一覧 -->
    <b-row>
      <b-col cols="12" xl="1"></b-col>
      <b-col cols="12" xl="10">
        <label class="title-fw title-fs-2">{{
          $t("COSTITEMCONTRACTLIST")
        }}</label>
      </b-col>
    </b-row>
    <br />
    <b-table
      ref="table"
      :fields="fields"
      :items="tableData"
      sticky-header
      responsive="sm"
    >
      <template v-slot:cell(Item1)="item">{{ item.value.value }}</template>
      <template v-slot:cell(Item2)="item">
        <b-link
          @click="gotoContractList"
          href="javascript:;"
          class="card-link link"
        >
          <u>{{ item.value.value }}</u>
        </b-link>
      </template>
      <template v-slot:cell(Item3)="item">{{ item.value.value }}</template>
      <template v-slot:cell(Item4)="item">{{ item.value.value }}</template>
    </b-table>

    <!-- ページャー＆表示件数 -->
    <br />
    <b-row>
      <b-col cols="12" xl="3"></b-col>
      <b-col cols="12" xl="5">
        <b-pagination
          id="message-list"
          v-if="tableData.length != 0"
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          align="center"
        ></b-pagination>
      </b-col>
      <b-col cols="12" xl="3">
        <div class="countList" align="right">
          <label>{{ $t("DISPLAY") }}</label
          >&nbsp;&nbsp;
          <label>50 | </label>
          <b-link href="javascript:;">100 | </b-link>
          <b-link href="javascript:;">150</b-link>
        </div>
      </b-col>
      <b-col cols="12" xl="1"></b-col>
    </b-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dateFrom: "",
      dateTo: "",
      formatted: "",
      selected: "",
      tableData: [],
      currentPage: 1,
      perPage: 2,
    };
  },
  computed: {
    rows() {
      return this.tableData.length;
    },
    lists() {
      const items = this.tableData;
      return items.slice(
        (this.currentPage - 1) * this.perPage,
        this.currentPage * this.perPage
      );
    },
    fields() {
      return [
        {
          key: "Item1",
          label: this.$t("ACCOUNT"),
          class: "text-center normal-width",
        },
        {
          key: "Item2",
          label: this.$t("CONTRACTFEEITEM"),
          class: "text-center normal-width",
        },
        {
          key: "Item3",
          label: this.$t("NUMBEROFCONTRACTSWON"),
          class: "text-center normal-width",
        },
        {
          key: "Item4",
          label: this.$t("CONTRACTACQUISITIONRATE"),
          class: "text-center normal-width",
        },
      ];
    },
  },
  mounted() {
    this.getTableData();
  },
  methods: {
    onContext(ctx) {
      this.formatted = ctx.selectedFormatted;
      this.selected = ctx.selectedYMD;
    },
    getTableData() {
      this.tableData = [
        {
          Item1: {
            value: "通信費",
            isNegative: false,
          },
          Item2: {
            value: "電話代",
            isNegative: false,
          },
          Item3: {
            value: "100",
            isNegative: false,
          },
          Item4: {
            value: "15.3％",
            isNegative: false,
          },
        },
        {
          Item1: {
            value: "通信費",
            isNegative: false,
          },
          Item2: {
            value: "携帯代",
            isNegative: false,
          },
          Item3: {
            value: "70",
            isNegative: false,
          },
          Item4: {
            value: "12.4％",
            isNegative: false,
          },
        },
        {
          Item1: {
            value: "水道光熱費",
            isNegative: false,
          },
          Item2: {
            value: "電気代",
            isNegative: false,
          },
          Item3: {
            value: "60",
            isNegative: false,
          },
          Item4: {
            value: "11.7％",
            isNegative: false,
          },
        },
        {
          Item1: {
            value: "消耗品費",
            isNegative: false,
          },
          Item2: {
            value: "トナー代",
            isNegative: false,
          },
          Item3: {
            value: "55",
            isNegative: false,
          },
          Item4: {
            value: "10.5％",
            isNegative: false,
          },
        },
      ];
      this.tableData.forEach((element, index) => {
        if (index === this.tableData.length - 1) {
          this.tableData[index].last_element = true;
          return;
        }
      });
    },
    gotoContractList() {
      this.$router.push({ name: "cost_item_wise_contract_list" });
    },
  },
};
</script>


<style>
.hrBarType {
  height: 2px;
  border-width: 10;
  color: gray;
  background-color: gray;
}
</style>